import { Button, Card, CardContent, CardHeader, CardTitle, Input } from '@/components/ui';
import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { MOCK_SUPPLIERS, PRODUCT_CATEGORIES, VAT_RATES } from './mock-data';
import type { ProductCategory, VatRate } from './types';

interface FormData {
  category: ProductCategory;
  sku: string;
  barcode: string;
  manufacturerCode: string;
  name: string;
  shortName: string;
  description: string;
  brand: string;
  model: string;
  purchasePrice: string;
  sellingPriceNet: string;
  vatRate: VatRate;
  minStockLevel: string;
  reorderQuantity: string;
  location: string;
  supplierId: string;
  // Rental fields
  rentalDailyRate: string;
  rentalWeeklyRate: string;
  rentalMonthlyRate: string;
  rentalDepositAmount: string;
}

export function ProductCreatePage() {
  const navigate = useNavigate();
  const [formData, setFormData] = useState<FormData>({
    category: 'power_tool',
    sku: '',
    barcode: '',
    manufacturerCode: '',
    name: '',
    shortName: '',
    description: '',
    brand: '',
    model: '',
    purchasePrice: '',
    sellingPriceNet: '',
    vatRate: 27,
    minStockLevel: '5',
    reorderQuantity: '10',
    location: '',
    supplierId: '',
    rentalDailyRate: '',
    rentalWeeklyRate: '',
    rentalMonthlyRate: '',
    rentalDepositAmount: '',
  });

  const updateField = <K extends keyof FormData>(field: K, value: FormData[K]) => {
    setFormData(prev => ({ ...prev, [field]: value }));
  };

  const calculateGrossPrice = () => {
    const net = parseFloat(formData.sellingPriceNet) || 0;
    return Math.round(net * (1 + formData.vatRate / 100));
  };

  const calculateMargin = () => {
    const purchase = parseFloat(formData.purchasePrice) || 0;
    const selling = parseFloat(formData.sellingPriceNet) || 0;
    if (purchase === 0) return 0;
    return (((selling - purchase) / purchase) * 100).toFixed(1);
  };

  const formatPrice = (price: number) => {
    return new Intl.NumberFormat('hu-HU', {
      style: 'currency',
      currency: 'HUF',
      maximumFractionDigits: 0,
    }).format(price);
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    if (!formData.name.trim()) {
      alert('A nv megadsa ktelez!');
      return;
    }

    if (!formData.sku.trim()) {
      alert('A cikkszm megadsa ktelez!');
      return;
    }

    // In real app, this would call API
    alert(`Cikk ltrehozva!\nCikkszm: ${formData.sku}\nNv: ${formData.name}`);
    navigate('/products');
  };

  const isRentalEquipment = formData.category === 'rental_equipment';

  return (
    <div className="min-h-screen kgc-bg">
      {/* Header */}
      <header className="shadow-sm kgc-card-bg">
        <div className="mx-auto flex max-w-4xl items-center justify-between px-4 py-4 sm:px-6 lg:px-8">
          <div className="flex items-center gap-3">
            <Button variant="ghost" onClick={() => navigate('/products')}>
               Vissza
            </Button>
            <div>
              <h1 className="text-xl font-bold text-gray-900 dark:text-gray-100">j cikk</h1>
              <p className="text-sm text-gray-500 dark:text-gray-400">Termk adatok rgztse</p>
            </div>
          </div>
          <Button onClick={handleSubmit} className="bg-kgc-primary hover:bg-kgc-primary/90">
            Ments
          </Button>
        </div>
      </header>

      <main className="mx-auto max-w-4xl px-4 py-8 sm:px-6 lg:px-8">
        <form onSubmit={handleSubmit} className="space-y-6">
          {/* Category */}
          <Card>
            <CardHeader>
              <CardTitle>Kategria</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="flex flex-wrap gap-2">
                {PRODUCT_CATEGORIES.map(cat => (
                  <button
                    key={cat.value}
                    type="button"
                    onClick={() => updateField('category', cat.value)}
                    className={`rounded-full px-4 py-2 text-sm font-medium transition-colors ${
                      formData.category === cat.value
                        ? cat.color
                        : 'bg-gray-100 text-gray-600 hover:bg-gray-200 dark:bg-slate-700 dark:text-gray-300 dark:hover:bg-slate-600'
                    }`}
                  >
                    {cat.label}
                  </button>
                ))}
              </div>
            </CardContent>
          </Card>

          {/* Basic info */}
          <Card>
            <CardHeader>
              <CardTitle>Azonostk</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="grid gap-4 sm:grid-cols-2">
                <div>
                  <label className="mb-2 block text-sm font-medium text-gray-700 dark:text-gray-300">
                    Cikkszm (bels) *
                  </label>
                  <Input
                    value={formData.sku}
                    onChange={e => updateField('sku', e.target.value)}
                    placeholder="MAK-DHP481"
                    required
                  />
                </div>
                <div>
                  <label className="mb-2 block text-sm font-medium text-gray-700 dark:text-gray-300">
                    Vonalkd (EAN)
                  </label>
                  <Input
                    value={formData.barcode}
                    onChange={e => updateField('barcode', e.target.value)}
                    placeholder="0088381678940"
                  />
                </div>
                <div>
                  <label className="mb-2 block text-sm font-medium text-gray-700 dark:text-gray-300">
                    Gyrti cikkszm
                  </label>
                  <Input
                    value={formData.manufacturerCode}
                    onChange={e => updateField('manufacturerCode', e.target.value)}
                    placeholder="DHP481RTJ"
                  />
                </div>
                <div>
                  <label className="mb-2 block text-sm font-medium text-gray-700 dark:text-gray-300">
                    Helykd (K-P-D)
                  </label>
                  <Input
                    value={formData.location}
                    onChange={e => updateField('location', e.target.value)}
                    placeholder="A-02-03"
                  />
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Product details */}
          <Card>
            <CardHeader>
              <CardTitle>Termk adatok</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="grid gap-4 sm:grid-cols-2">
                <div className="sm:col-span-2">
                  <label className="mb-2 block text-sm font-medium text-gray-700 dark:text-gray-300">
                    Megnevezs *
                  </label>
                  <Input
                    value={formData.name}
                    onChange={e => updateField('name', e.target.value)}
                    placeholder="Makita DHP481RTJ akkus tvefr-csavaroz"
                    required
                  />
                </div>
                <div>
                  <label className="mb-2 block text-sm font-medium text-gray-700 dark:text-gray-300">
                    Rvid nv
                  </label>
                  <Input
                    value={formData.shortName}
                    onChange={e => updateField('shortName', e.target.value)}
                    placeholder="DHP481RTJ"
                  />
                </div>
                <div>
                  <label className="mb-2 block text-sm font-medium text-gray-700 dark:text-gray-300">
                    Mrka
                  </label>
                  <Input
                    value={formData.brand}
                    onChange={e => updateField('brand', e.target.value)}
                    placeholder="Makita"
                  />
                </div>
                <div>
                  <label className="mb-2 block text-sm font-medium text-gray-700 dark:text-gray-300">
                    Tpus/Modell
                  </label>
                  <Input
                    value={formData.model}
                    onChange={e => updateField('model', e.target.value)}
                    placeholder="DHP481RTJ"
                  />
                </div>
                <div>
                  <label className="mb-2 block text-sm font-medium text-gray-700 dark:text-gray-300">
                    Beszllt
                  </label>
                  <select
                    value={formData.supplierId}
                    onChange={e => updateField('supplierId', e.target.value)}
                    className="w-full rounded-md border px-3 py-2 border-gray-300 dark:border-slate-600 bg-white dark:bg-slate-800 text-gray-900 dark:text-gray-100"
                  >
                    <option value="">-- Vlasszon --</option>
                    {MOCK_SUPPLIERS.map(s => (
                      <option key={s.id} value={s.id}>
                        {s.name}
                      </option>
                    ))}
                  </select>
                </div>
                <div className="sm:col-span-2">
                  <label className="mb-2 block text-sm font-medium text-gray-700 dark:text-gray-300">
                    Lers
                  </label>
                  <textarea
                    value={formData.description}
                    onChange={e => updateField('description', e.target.value)}
                    className="w-full rounded-md border p-3 border-gray-300 dark:border-slate-600 bg-white dark:bg-slate-800 text-gray-900 dark:text-gray-100"
                    rows={3}
                    placeholder="Rszletes termklers..."
                  />
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Pricing */}
          <Card>
            <CardHeader>
              <CardTitle>razs</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="grid gap-4 sm:grid-cols-2 lg:grid-cols-4">
                <div>
                  <label className="mb-2 block text-sm font-medium text-gray-700 dark:text-gray-300">
                    Beszerzsi r (nett)
                  </label>
                  <Input
                    type="number"
                    value={formData.purchasePrice}
                    onChange={e => updateField('purchasePrice', e.target.value)}
                    placeholder="89000"
                    min={0}
                  />
                </div>
                <div>
                  <label className="mb-2 block text-sm font-medium text-gray-700 dark:text-gray-300">
                    Eladsi r (nett)
                  </label>
                  <Input
                    type="number"
                    value={formData.sellingPriceNet}
                    onChange={e => updateField('sellingPriceNet', e.target.value)}
                    placeholder="125000"
                    min={0}
                  />
                </div>
                <div>
                  <label className="mb-2 block text-sm font-medium text-gray-700 dark:text-gray-300">
                    FA kulcs
                  </label>
                  <select
                    value={formData.vatRate}
                    onChange={e => updateField('vatRate', parseInt(e.target.value) as VatRate)}
                    className="w-full rounded-md border px-3 py-2 border-gray-300 dark:border-slate-600 bg-white dark:bg-slate-800 text-gray-900 dark:text-gray-100"
                  >
                    {VAT_RATES.map(v => (
                      <option key={v.value} value={v.value}>
                        {v.label}
                      </option>
                    ))}
                  </select>
                </div>
                <div>
                  <label className="mb-2 block text-sm font-medium text-gray-700 dark:text-gray-300">
                    Eladsi r (brutt)
                  </label>
                  <div className="rounded-md border px-3 py-2 bg-gray-50 dark:bg-slate-700 text-gray-900 dark:text-gray-100 font-medium">
                    {formatPrice(calculateGrossPrice())}
                  </div>
                </div>
              </div>
              <div className="mt-4 rounded-lg bg-green-50 p-3 dark:bg-green-900/20">
                <p className="text-sm text-green-700 dark:text-green-300">
                  Szmtott rrs: <strong>{calculateMargin()}%</strong>
                </p>
              </div>
            </CardContent>
          </Card>

          {/* Stock settings */}
          <Card>
            <CardHeader>
              <CardTitle>Kszlet belltsok</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="grid gap-4 sm:grid-cols-2">
                <div>
                  <label className="mb-2 block text-sm font-medium text-gray-700 dark:text-gray-300">
                    Minimum kszletszint
                  </label>
                  <Input
                    type="number"
                    value={formData.minStockLevel}
                    onChange={e => updateField('minStockLevel', e.target.value)}
                    min={0}
                  />
                </div>
                <div>
                  <label className="mb-2 block text-sm font-medium text-gray-700 dark:text-gray-300">
                    Rendelsi mennyisg
                  </label>
                  <Input
                    type="number"
                    value={formData.reorderQuantity}
                    onChange={e => updateField('reorderQuantity', e.target.value)}
                    min={0}
                  />
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Rental settings */}
          {isRentalEquipment && (
            <Card>
              <CardHeader>
                <CardTitle>Brlsi belltsok</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="grid gap-4 sm:grid-cols-2 lg:grid-cols-4">
                  <div>
                    <label className="mb-2 block text-sm font-medium text-gray-700 dark:text-gray-300">
                      Napi dj (Ft)
                    </label>
                    <Input
                      type="number"
                      value={formData.rentalDailyRate}
                      onChange={e => updateField('rentalDailyRate', e.target.value)}
                      placeholder="8500"
                      min={0}
                    />
                  </div>
                  <div>
                    <label className="mb-2 block text-sm font-medium text-gray-700 dark:text-gray-300">
                      Heti dj (Ft)
                    </label>
                    <Input
                      type="number"
                      value={formData.rentalWeeklyRate}
                      onChange={e => updateField('rentalWeeklyRate', e.target.value)}
                      placeholder="45000"
                      min={0}
                    />
                  </div>
                  <div>
                    <label className="mb-2 block text-sm font-medium text-gray-700 dark:text-gray-300">
                      Havi dj (Ft)
                    </label>
                    <Input
                      type="number"
                      value={formData.rentalMonthlyRate}
                      onChange={e => updateField('rentalMonthlyRate', e.target.value)}
                      placeholder="140000"
                      min={0}
                    />
                  </div>
                  <div>
                    <label className="mb-2 block text-sm font-medium text-gray-700 dark:text-gray-300">
                      Kauci sszeg (Ft)
                    </label>
                    <Input
                      type="number"
                      value={formData.rentalDepositAmount}
                      onChange={e => updateField('rentalDepositAmount', e.target.value)}
                      placeholder="150000"
                      min={0}
                    />
                  </div>
                </div>
              </CardContent>
            </Card>
          )}

          {/* Actions */}
          <div className="flex justify-end gap-3">
            <Button type="button" variant="outline" onClick={() => navigate('/products')}>
              Mgse
            </Button>
            <Button type="submit" className="bg-kgc-primary hover:bg-kgc-primary/90">
              Cikk ltrehozsa
            </Button>
          </div>
        </form>
      </main>
    </div>
  );
}
