// Inventory types

export enum StockStatus {
  IN_STOCK = 'IN_STOCK',
  LOW_STOCK = 'LOW_STOCK',
  OUT_OF_STOCK = 'OUT_OF_STOCK',
  DISCONTINUED = 'DISCONTINUED',
}

export enum MovementType {
  IN = 'IN', // Bevtelezs
  OUT = 'OUT', // Kiads
  ADJUSTMENT = 'ADJUSTMENT', // Korrekci
  TRANSFER = 'TRANSFER', // thelyezs
  RETURN = 'RETURN', // Visszavtel
}

export enum ItemCategory {
  POWER_TOOL = 'POWER_TOOL', // Elektromos szerszm
  HAND_TOOL = 'HAND_TOOL', // Kziszerszm
  ACCESSORY = 'ACCESSORY', // Tartozk
  CONSUMABLE = 'CONSUMABLE', // Fogyeszkz
  WORKWEAR = 'WORKWEAR', // Munkaruha
  SPARE_PART = 'SPARE_PART', // Alkatrsz
}

export interface InventoryItem {
  id: string;
  sku: string;
  barcode?: string;
  name: string;
  description?: string;
  category: ItemCategory;
  brand?: string;
  unit: string;

  // Stock levels
  currentStock: number;
  reservedStock: number; // Foglalt (brlshez, rendelshez)
  availableStock: number; // Elrhet = current - reserved
  minStock: number; // Minimum kszlet (figyelmeztets)
  maxStock: number; // Maximum kszlet

  // Pricing
  purchasePrice: number; // Beszerzsi r
  sellingPrice: number; // Eladsi r
  rentalPriceDaily?: number; // Napi brleti dj
  vatRate: number;

  // Location
  warehouseId: string;
  warehouseName: string;
  location?: string; // Polc/hely

  // Status
  status: StockStatus;
  isRentable: boolean;
  isSellable: boolean;

  // Dates
  lastStockUpdate: string;
  lastInventoryCheck?: string;
}

export interface StockMovement {
  id: string;
  itemId: string;
  itemName: string;
  itemSku: string;
  type: MovementType;
  quantity: number;
  previousStock: number;
  newStock: number;
  reference?: string; // Hivatkozs (rendels, munkalap, stb.)
  note?: string;
  createdBy: string;
  createdByName: string;
  createdAt: string;
}

export interface Warehouse {
  id: string;
  name: string;
  code: string;
  address?: string;
  isDefault: boolean;
}
