/**
 * Munkalap sttuszok
 */
export enum WorksheetStatus {
  FELVEVE = 'FELVEVE',
  FOLYAMATBAN = 'FOLYAMATBAN',
  VARHATO = 'VARHATO',
  KESZ = 'KESZ',
  SZAMLAZANDO = 'SZAMLAZANDO',
  LEZART = 'LEZART',
  TOROLVE = 'TOROLVE',
}

/**
 * Munkalap tpusok
 */
export enum WorksheetType {
  FIZETOS = 'FIZETOS',
  GARANCIALIS = 'GARANCIALIS',
  BERLESI = 'BERLESI',
  KARBANTARTAS = 'KARBANTARTAS',
}

/**
 * Prioritsok
 */
export enum WorksheetPriority {
  SURGOS = 'SURGOS',
  NORMAL = 'NORMAL',
  GARANCIALIS = 'GARANCIALIS',
}

/**
 * Termk/Gp a szervizhez
 */
export interface Product {
  id: string;
  name: string;
  brand?: string;
  model?: string;
  serialNumber?: string;
  category: ProductCategory;
  warrantyExpiry?: Date;
  purchaseDate?: Date;
}

export enum ProductCategory {
  POWER_TOOL = 'POWER_TOOL',
  GARDEN = 'GARDEN',
  CONSTRUCTION = 'CONSTRUCTION',
  CLEANING = 'CLEANING',
  OTHER = 'OTHER',
}

/**
 * Alkatrsz
 */
export interface Part {
  id: string;
  name: string;
  sku: string;
  unitPrice: number;
  inStock: number;
  category: string;
}

/**
 * Munkadj norma (Makita style)
 */
export interface LaborNorm {
  id: string;
  code: string;
  description: string;
  minutes: number;
  hourlyRate: number;
  calculatedPrice: number;
}

/**
 * Munkalap ttel (alkatrsz vagy munkadj)
 */
export interface WorksheetItem {
  id: string;
  type: 'ALKATRESZ' | 'MUNKADIJ' | 'EGYEB';
  description: string;
  quantity: number;
  unitPrice: number;
  vatRate: number;
  netAmount: number;
  grossAmount: number;
  partId?: string;
  normId?: string;
}

/**
 * Wizard llapot
 */
export interface WorksheetWizardState {
  step: number;
  // Partner
  partnerId: string | null;
  partnerName: string;
  // Gp
  product: Product | null;
  deviceName: string;
  serialNumber: string;
  // Problma
  worksheetType: WorksheetType;
  priority: WorksheetPriority;
  faultDescription: string;
  costLimit: number | null;
  // Diagnosztika
  diagnosis: string;
  workPerformed: string;
  items: WorksheetItem[];
  // sszests
  estimatedCompletionDate: Date | null;
  internalNote: string;
}

