/**
 * Teljes munkalap rekord (wizard state-bl generlt)
 */
export interface Worksheet {
  id: string;
  worksheetNumber: string;
  // Partner
  partnerId: string;
  partnerName: string;
  partnerPhone?: string;
  partnerEmail?: string;
  // Gp/Termk
  product: Product;
  // Munka rszletek
  worksheetType: WorksheetType;
  priority: WorksheetPriority;
  status: WorksheetStatus;
  faultDescription: string;
  diagnosis: string;
  workPerformed: string;
  // Ttelek
  items: WorksheetItem[];
  // Pnzgyi
  costLimit: number | null;
  netTotal: number;
  vatTotal: number;
  grossTotal: number;
  depositPaid: number;
  // Dtumok
  createdAt: string;
  updatedAt: string;
  estimatedCompletionDate: string | null;
  completedAt: string | null;
  invoicedAt: string | null;
  // Szemlyek
  createdBy: string;
  assignedTo: string | null;
  // Megjegyzsek
  internalNote: string;
  customerNote: string;
  // Trtnet
  history: WorksheetHistoryEntry[];
}

/**
 * Munkalap trtnet bejegyzs
 */
export interface WorksheetHistoryEntry {
  id: string;
  timestamp: string;
  action: WorksheetHistoryAction;
  description: string;
  userId: string;
  userName: string;
  oldValue?: string;
  newValue?: string;
}

export enum WorksheetHistoryAction {
  CREATED = 'CREATED',
  STATUS_CHANGED = 'STATUS_CHANGED',
  ITEM_ADDED = 'ITEM_ADDED',
  ITEM_REMOVED = 'ITEM_REMOVED',
  ASSIGNED = 'ASSIGNED',
  NOTE_ADDED = 'NOTE_ADDED',
  INVOICED = 'INVOICED',
  COMPLETED = 'COMPLETED',
}

